* {
  box-sizing: border-box;
}

.issue-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.issue-col {
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}

.issue-mid-col {
  border-right: 1px solid black;
}

.comment-textArea {
  width: 60%;
}

.comment-buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.issue-delete-btn {
  margin-left: 10px;
}

.issue-title-read, .issue-title-unread {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
}

.issue-title-unread {
  font-weight: bold;
}

.issue-title-read {
  font-weight: normal;
}

.issue-createdat {
  font-weight: normal;
  font-size: 0.7em;
  margin-left: 10px;
  padding-top: 5px;
  font-style: italic;
}

.issue-overdue {
  font-weight: normal;
  font-size: 0.7em;
  margin-left: 10px;
  padding-top: 5px;
  color: red;
  font-weight: bold;
}

.resolved {
  color: lightgray;
  text-decoration: line-through;
}

.font-bold {
  font-weight: bold;
  color: #585753;
  background-color: #F7F7F7;
  border: 1px solid lightgray;
  padding: 5px 10px;
  margin: 10px;
}

.issue-comments {
  display: flex;
  flex-direction: column;
}

.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
  color: black
}

.tooltip .tooltiptext {
  /* visibility: hidden; */
  display: none;
  width: 120px;
  background-color: black;
  color: black;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  top: -5px;
  right: 505%;
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  /* visibility: visible; */
  display: inline-block;
}